import h from 'virtual-dom/h';
import {Observable} from 'rx';

import {posts_} from '../models/posts';
import {formatDate} from '../utils';
import {clicksByClass_} from '../events';
import {markPostAsRead_} from '../models/posts';

let postView = (post) =>
    h('article', {className: "post-item post"}, [
      h('header', {className: "post-item-header"}, [
        h('h2', {className: "post-item-title"}, [
          h('a', {className: "post-title", href: post.link}, [post.title])
          ])
      ]),
      h('section', {className: "post-item-excerpt"}, [
        post.contentSnippet
      ]),

      h('footer', {className: "post-item-footer"}, [
        h('ul', {className: "post-item-meta-list"}, [
          h('li', {className: "post-item-meta-item"}, [
            h('p', null, [h('a', {href: post.link}, [post.author])
            ])
          ]),

          h('li', {className: "post-item-meta-item"}

          ),

          h('li', {className: "post-item-meta-item"}, [
            h('p', {itemprop: "articleSection"}, [post.categories.join(', ')])
          ])
        ])
      ])
    ])
  ;


let view = (postViews) =>
  h('section', {className: "post-list"}, [
    postViews
  ]);

let render_ = (feedFilters_) => Observable
      .combineLatest(
        posts_,
        feedFilters_(),
        (posts, filters) => posts.filter(post => {
          let result = true;
          let checks = Object.keys(filters);
          for(let key of checks) {
            if(post[key] !== filters[key])
              result = false;
          }

          return result;
        })
      )
      .map(posts => posts.map(postView))
      .map(view)
      .startWith(view());

let readPost_ = () => {
  let readPostClicks_ = clicksByClass_('post-title');
  let posts_ = readPostClicks_
        .do(e => e.preventDefault())
        .map(e => e.target.href)
        .flatMap(link => Posts.get(link))
        .do(markPostAsRead_);

  return posts_;
}

export default render_;
export {readPost_};



