import h from 'virtual-dom/h';
import {Observable} from 'rx';

import postsList_, {readPost_} from './posts-list';
import sidebar_, {feedFilters_} from './sidebar';
import reader_ from './reader';

let view = (postsList, sidebar, reader) =>
      h('div', {id: "container", className: "container"}, [
         h('div', {className: "surface"}, [
           h('div', {className: "surface-container"}, [
             h('div', {className: "content"}, [
               h('aside', {className: "cover"}, [sidebar]),
               h('div', {className: "reader"}, [reader]),
               h('div', {className: "wrapper"}, [
                 h('div', {className: "wrapper-container"}, [postsList])
               ])
             ])
           ])
         ])
       ])
  ;

let render_ = () => Observable.combineLatest(
  postsList_(feedFilters_),
  sidebar_(),
  reader_(readPost_),
  view
);


export default render_;



