import h from 'virtual-dom/h';
import {Observable} from 'rx';

import {clicksByClass_} from '../events';
import {dataAttrAsClass} from '../utils';

let view = () =>
  h('ul', {className: "sidebar-controls"}, [
    h('li', {className: "sidebar-control filter-posts data-filter-all"}, ["All"]),
    h('li', {className: "sidebar-control filter-posts active data-filter-unread"}, ["Unread"]),
    h('li', {className: "sidebar-control filter-posts data-filter-read"}, ["Read"])
  ])
  ;

let feedFilters_ = () => {
  let widgetClicks_ = clicksByClass_('filter-posts');

  let filters_ = widgetClicks_
        .map(e => e.target)
        .map(el => dataAttrAsClass('filter', el));

  return filters_;
};

let render_ = () => {
  let widgetClicks_ = clicksByClass_('filter-posts');

  widgetClicks_
    .map(e => e.target)
    .do(el => {
      document.querySelector('.filter-posts.active').classList.remove('active');
      el.classList.add('active');
    })
    .subscribe();

  return Observable.return(view());
}


export default render_;
export {feedFilters_};



